.TodoItem{
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgb(32, 35, 41, 0.15);
}

.TodoItem-p{
    margin: 24px, 0px, 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete{
    text-decoration: line-through;
}

.Icon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /*background-color: #CCC;*/
}

.Icon-check{
    position: absolute;
    left: 12px;
}
.Icon-check--activate{
    color:  green;;
}

.Icon-check:hover{
    color: green;
}

.Icon-delete{
    position: absolute;
    top: -24px;
    right: 0;
    font-size: 32px;
}
.Icon-delete:hover{
    color: red;
}
